<template>
  <section class="content">
    <div class="exit__user-wrapper">
      <LogoPage :logo="logo" title="базы данных"/>
      <div class="filters">
        <div class="filters__row">
          <div class="filter__row-container">
            <div v-for="(modal, index) in modalsFilters"
                 :key="`show-${index}`">
              <div v-if="index !== 'create'" class="filter__item"
                   @click="filterSelect(index)">
                {{ modal.field }}
              </div>
              <div v-else-if="rights.create" class="filter__item" @click="filterSelect(index)">
                {{ modal.field }}
              </div>
            </div>
          </div>
          <div class="filter__row-container">
            <div class="filter__toggle">
              <Toggle :items="['WEKESER', 'все']"/>
            </div>
          </div>
        </div>
        <div class="filters__row">
          <div class="filter__row-container">
            <Toggle :isFilter="true" :items="['тз', 'общеизв', 'изобрет', 'ПМ', 'пром.обр.']"/>
          </div>
        </div>
      </div>
      <ExitUser :name="userName"/>
    </div>
    <BasicWrapper>
      <div class="fields-objects">
        <div v-for="(header, index) in headers" :key="`detail-${index}`" class="detail-field">
          <div class="fields-objects__title"> {{ header['title'] }}</div>
          <div v-if="header['field'] === 'trademark'" class="fields-objects__description"><img
              :src="rows[header['field']]" alt=""></div>
          <div v-else class="fields-objects__description"> {{ rows[header['field']] }}</div>
        </div>
      </div>
      <NavItems/>
    </BasicWrapper>
    <Modal v-for="(modal, index) in modalsFilters" :key="index" v-model:isVisible="modal.show">
      <div class="filter__content">
        <input v-model="modal.value" type="text" @keyup.enter="filterSelect(index, modal.show)">
        <img alt="" src="../assets/icons/search.png" @click="filterSelect(index)">
      </div>
    </Modal>
  </section>
</template>

<script>
import DatabaseObjectApi from '../api/DatabaseObjectApi';
import UsersApi from "@/api/UsersApi";

export default {
  name: 'database-page',
  data() {
    return {
      year: '',
      search: '',
      affairs: '',
      logo: this.$icons.database,
      rights: UsersApi.rights,
      userName: UsersApi.name,
      modalsFilters: {
        search: {field: 'поиск', show: false, value: ''},
        year: {field: 'год', show: false, value: ''},
        affairs: {field: 'дело', show: false, value: ''}
      },
      headers: [
        {title: 'Название объекта', field: 'name'},
        {title: 'Товарный знак', field: 'trademark'},
        {title: 'Территория', field: 'territory'},
        {title: 'Неохраноспособные элементы', field: 'nonProtectedElements'},
        {title: 'Наименование и адресс владельца', field: 'ownerNameAndAddress'},
        {title: 'Номер заявки', field: 'applicationNumber'},
        {title: 'Дата заявки', field: 'dateNumber'},
        {title: 'Номер регистрации', field: 'numberRegistration'},
        {title: 'Дата регистрации', field: 'dateRegistration'},
        {title: 'Дата публикации', field: 'datePublication'},
        {title: 'Номер приоритета', field: 'priorityNumber'},
        {title: 'Дата приоритета', field: 'priorityDate1'},
        {title: 'Дата приоритета', field: 'priorityDate2'},
        {title: 'Начало срока действия', field: 'validityStart'},
        {title: 'Конец срока действия', field: 'validityEnd'},
        {title: 'Начало срока продления', field: 'extensionStart'},
        {title: 'Конец срока продления', field: 'extensionEnd'},
        {title: 'Классы МКТУ', field: 'niceClasses'},
      ],
      rows: {}
    }
  },
  mounted() {
    DatabaseObjectApi.getById(this.$route.params.id).then((response) => {
      if (response) {
        this.rows = response.data;
      }
    })
  },
  methods: {
    filterSelect(type, close = false) {
      this.modalsFilters[type].show = !close;
    },
    navigateToAffairs(id) {
      this.$router.push(`/affairs/object/${id}`);
    },
  }
}
</script>

<style scoped>
main {
  display: flex;
  justify-content: center;
}

.toggle {
  width: 100%;
}

.exit__user-wrapper {
  display: flex;
  justify-content: space-between;
}

input {
  border: unset;
  border-bottom: 1px solid #000;
}

.filter__content img {
  margin-left: 7px;
  width: 18px;
  cursor: pointer;
}
</style>
